  --alfavito-color: #5f6775;
  --button-color-orange: #fe7031;
  --button-color-white: #fff;
  --button-text-white: #fff;
  --button-text-color: #fd5103;
  --transition: 250ms cubic-bezier(.4, 0, .2, 1);
  --card-item: 3;
  --card-gap: 32px;
  --title-poppins: "Poppins";
  --text-roboto: "DM Sans", sans-serif;
}

.card-set {
  gap: var(--card-gap);
  flex-wrap: wrap;
  display: flex;
}

.card-set-item {
  flex-basis: calc((100% - (var(--card-item)  - 1) * var(--card-gap) ) / var(--card-item) );
}

body {
