.switcher {
  width: 40px;
  height: 20px;
  background: #fff;
  border-radius: 20px;
}

.switcher input[type="checkbox"] {
  width: 40px;
  height: 20px;
  -webkit-appearance: none;
  background: #fff;
  border: .604701px solid #fd5103;
  border-radius: 20px;
  outline: none;
  transition: all .5s;
  position: relative;
}

.switcher input:checked[type="checkbox"] {
  background: #fd5103;
}

.switcher input[type="checkbox"]:before {
  content: "";
  width: 16px;
  height: 16px;
  background: #fd5103;
  border-radius: 20px;
  transition: all .5s;
  position: absolute;
  top: 1px;
  left: 0;
  transform: scale(1.1);
}

.switcher input:checked[type="checkbox"]:before {
  background: #fff;
  left: 21px;
}

