.mob-container {
  position: absolute;
}

.mob-menu {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  opacity: 0;
  transform: translatex(100%);
  transition: transform var(--transition), opacity var(--transition);
  padding-right: 20px;
  padding-left: 20px;
  // background: transparent;
  background-color: #fff;
}

.mob-menu.is-open {
  position: fixed;
  transform: translatex(0%);
  opacity: 1;
  transition: transform var(--transition), opacity var(--transition);
  z-index: 99;
}

.mob-menu__header {
  display: flex;
  align-items: baseline;
  margin-top: 30px;
}

.mob-logo {
  display: none;

  @include mq(tab-only) {
    display: block;
  }
}

.mob-dark {
  @include mq(tab-only) {
    margin-left: 100px;
  }
}

.mob-nav {
  display: flex;
  flex-direction: column;
  gap: 32px;
}

.mob-nav__link {
  font-family: 'Poppins';
  font-weight: 400;
  font-size: 27px;
  line-height: 1.16;
  color: #000000;
  align-items: center;
  padding-top: 12px;
  padding-bottom: 12px;
  transition: color var(--transition);

  @include mq(fab) {
    font-size: 28px;
  }

  @include mq(tab) {
    font-size: 30px;
  }

  &:focus,
  &:hover {
    color: var(--button-text-color);
  }
}

.mob-nav__item {
  @include mq(tab-only) {
    margin-left: 300px;
  }
}

.btn-menu__icon {
  fill: currentColor;
}

.mob__field {
  display: block;
  max-width: 560px;
  margin-top: 40px;
  margin-bottom: 40px;
}

.form__input-span {
  position: relative;
}

.mob__icon-input {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  left: 0;
  top: 56%;
  height: 40px;
  width: 40px;
  transform: translateY(-50%);
  border-right: 1px solid var(--button-text-color);

  @include mq(tab) {
    display: none;
  }
}

.mob-input {
  padding-left: 50px;
  width: 100%;
  height: 40px;
  outline: none;
  font-family: 'Poppins';
  // font-weight: 400;
  font-size: 14px;
  line-height: 1.71;
  border: 1px solid var(--button-text-color);
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.15));
  border-radius: 4px;
  background-color: inherit;
  color: rgba(95, 103, 117, 0.4);
  transform: border-color var(--transition);

  &::placeholder {
    color: rgba(95, 103, 117, 0.4);
  }

  @include mq(tab) {
    display: none;
  }
}

.btn-menu__open {
  // position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  right: 10px;
  top: 30px;
  border: none;
  background: transparent;
  color: var(--background-dark);
  text-align: center;
  background-color: transparent;
  border: none;
  flex-shrink: 1;
  transition: color var(--transition);

  @include mq(desc) {
    display: none;
  }

  &:focus,
  &:hover {
    color: var(--button-text-color);
  }
}

.btn-menu__close {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  right: 10px;
  top: 30px;
  border: none;
  background: transparent;
  color: var(--background-dark);
  text-align: center;

  &:focus,
  &:hover {
    color: var(--button-text-color);
  }
}

.display-none {
  display: none;
}
